<template>
  <div class="wrap">
    <table class="mypage__table">
      <tr class="mypage__table__tr">
        <td>가입일자</td>        
        <td colspan="2">{{mypageInfo.registerDate}}</td>
      </tr>
      <tr class="mypage__table__tr">
        <td>이메일</td>        
        <td colspan="2">{{mypageInfo.email}}</td>
      </tr>
      <tr class="mypage__table__tr">
        <td>비밀번호</td>        
        <td colspan="2">****</td>    
      </tr>
      <tr class="mypage__table__tr">
        <td>닉네임</td>        
        <td colspan="2">{{mypageInfo.name}}</td>
      </tr>
      <tr class="mypage__table__tr">
        <td>기본배송지</td>        
        <td colspan="2">{{mypageInfo.delivery}}</td>  
      </tr>
      <tr>
        <td><button class="mypage__button" @click="openModal">회원탈퇴</button></td>
        <td></td>
        <td class="mypage__table__sortRight"><button class="mypage__button" @click="clickBtnChangeInfo">정보변경</button></td>
      </tr>
    </table>


    <!-- 탈퇴 클릭시 나오는 모달창 -->
    <div class="modal" :style="isDisplayModal">
        <div class="modalBox">
          <div class="modal__btnX" @click="closeModal">×</div>
          <table class="modal__table">
            <tr>
              <td colspan="2"><div class="modal__announce">탈퇴하시려면 비밀번호를 다시 한 번 입력해주세요</div></td>
            </tr>
            <tr>
              <td>이메일</td>
              <td><input readonly class="modal__email" :value="`${mypageInfo.email}`"></td>
            </tr>
            <tr>
              <td>비밀번호</td>
              <td><input type="password" id="modal__password" class="modal__password" v-model="AccountDelPw"></td>
            </tr>
          </table>
          <div class="modal__button"><button :disabled="btnAccountDel" class="mypage__button" @click="clickBtnAccountDel">회원탈퇴</button></div>
          
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name : 'MyPage',
  data() {
    return {
      firebase : this.$store.state.firebase,
      db : this.$store.state.db,
      loginUserInfo : localStorage.getItem('user'),
      mypageInfo : [],
      isDisplayModal : "display:none",
      AccountDelPw : '',
      btnAccountDel : false,

    }
  },
  beforeCreate () { 
    if (localStorage.getItem('user') == undefined) { 
      alert('로그인 후 이용할 수 있습니다.')
      this.$router.push('/login')
      //현재 서버가 없기때문에 임시로 라이프사이클을 이용해 리다이렉트 처리
    } 
  },
  beforeMount(){
    if(localStorage.getItem('user') != undefined){

      this.db.collection('user').doc(JSON.parse(this.loginUserInfo).uid).get()
      .then((result)=>{
        this.mypageInfo = result.data();
        this.mypageInfo.registerDate = this.mypageInfo.registerDate.toDate().toLocaleString();
      })
    }
    
  },
  methods: {
    firebaseAccountDel(){
      
      if(this.AccountDelPw == ''){
        return false;
      }else{
        const user = this.firebase.auth().currentUser;

        //firebase 정책상 보안에 민감한 행위들에 한해 재인증이 필요함
        const credential = this.firebase.auth.EmailAuthProvider.credential(
            this.mypageInfo.email,
            this.AccountDelPw
        );

        user.reauthenticateWithCredential(credential)
        .then(()=>{
          //회원탈퇴
          user.delete().then(() => {
            
            alert('탈퇴되었습니다.')
            localStorage.removeItem('user');  
            //세션대용으로 쓰고있는 로컬스토리지에서 로그인정보 삭제

            this.$router.push('/').then(()=>{
              this.$router.go(0);
            })

          }).catch((error) => {
            alert(error)
          });
        }).catch(()=>{
          alert('비밀번호가 틀렸습니다.')
        })
      }

    },
    clickBtnChangeInfo(){
      this.$router.push('/mypage/modify');
    },
    openModal(){
      this.isDisplayModal = 'display:block';
    },
    closeModal(){
      this.isDisplayModal = 'display:none';
    },
    deleteUserTable(){
      if(confirm('정말 탈퇴하시겠습니까?')){
        this.btnAccountDel = true;  //완료될때까지 버튼 비활성화

        //user테이블의 데이터삭제
        this.db.collection('user').doc(JSON.parse(this.loginUserInfo).uid).delete()
        .then(()=>{
          this.firebaseAccountDel();
          //반대 순서로 하면 테이블삭제 진행이 안되는 문제가 있어서 테이블삭제->탈퇴 순서로 진행함
        }).catch((error)=>{
          console.log(error)
        })
      }
    },
    clickBtnAccountDel(){
      this.deleteUserTable();
      //this.btnAccountDel = false;
    },

  },
}
</script>

<style scoped>
  :root{
    --text-color:#f0f4f5;
    --background-color : #263343;
    --accent-color : #d49466;
    --border-color : #D5D5D5;
  }
  .wrap{
    display: flex;
    justify-content: center;
    padding-top: 30px;
    overflow: auto;
  }
  .mypage__table{
    border-collapse: collapse;
    background: white;
    /* max-width: 480px;
    min-width: 390px; */
    width: 480px; 
    table-layout: fixed;
    border-radius: 5px;
  }
  .mypage__table__tr{
    border-bottom: 1px solid #D5D5D5;
    
  }
  .mypage__table td{
    padding: 10px;
  }
  .mypage__button {
    background-color: var(--background-color);
    border: 1px solid #323f6b;
    color: #ffffff;
    border-radius: 4px;
    padding: 5px 8px;
    font-size: 11pt;
    font-family: 'NanumBarunGothic';
    cursor: pointer;
  }

  .modal {
    z-index: 500;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
  }
  .modalBox {
    position: relative;
    top : 20%;
    left : 50%;
    transform: translate(-50%);
    position : relative;
    width:350px;
    height: 180px;
    background: white;
    border-radius: 5px;
  }
  .modal__btnX{
    cursor: pointer;
    text-align: end;
    padding: 10px;
    font-size: 15pt;
  }
  .modal__email{
    margin-left: 10px;
    height: 20px;
    font-size: 11pt;
  }
  .modal__password{
    margin-left: 10px;
    height: 20px;
    font-size: 11pt;
  }
  .modal__announce{
    font-size: 11pt;
    padding-bottom: 15px;
  }
  .modal__table{
    margin-left: 15px;
  }
  .modal__button{
    text-align: end;
    margin-right: 8px;
    margin-top: 7px;
  }
  button:disabled{
  background: #3A4E66;
  cursor: default;
  }
  .mypage__table__sortRight{
    text-align: end;
  }

</style>